import lifecycle from '../core/lifecycle';
import storage from '../core/storage';
import { subscribe, generateChannelName } from './comm';
import { qs } from '../core/dom/queryselect';
import createQR from '../core/misc/qr';
import { onEvent } from '../core/dom/events';

let sync;
let hostname;
let srvConfig;
let content;
let data;
let prio;
let picassoVersion;

/**
 * Start syncing
 */
function startSync(channelName) {
  // Store the channel name in localStorage
  storage.sync.channelName = channelName;

  if (sync) {
    sync.close();
  }

  // Subscribe and set the sync object
  sync = subscribe(channelName, hostname);

  // Setup a re-sync on "refresh" msg (when a bare-client subscribes)
  sync.onmessage = function onMsg(msg) {
    if (msg.type === 'refresh') {
      sync({ content, data, prio, picassoVersion });
    } else if (msg.type === 'server-shutdown') {
      lifecycle.trigger('server-shutdown');
    }
  };

  // Show the "Sync connected area"
  qs('#syncconnectedarea').className = '';

  // Create the URL which bare clients use
  const bareClientUrl = `http://${hostname}/${srvConfig.bareclient || 'app/bare-client.html'}?channel=${sync.channel}`;

  // Set the bare client URL on text input & generate QR code
  qs('#bareclienturl').value = bareClientUrl;
  createQR(qs('#syncqr'), bareClientUrl);

  // Finish up by doing a sync on connect
  sync({ content, data, prio, picassoVersion });
}

/**
 * Handle code and data updates
 */
lifecycle.on(['code', 'data', 'rendererprio'], (obj) => {
  if (obj.content) {
    content = obj.content;
  }
  if (obj.data) {
    data = obj.data;
  }
  if (obj.prio) {
    prio = obj.prio;
  }

  if (sync) {
    sync(obj);
  }
});

/**
 * Handle picasso version change
 */
lifecycle.on('picassoload', (newPicasso, version) => {
  picassoVersion = version;
  if (sync) {
    sync({ picassoVersion });
  }
});

lifecycle.on('srv-config', (newConfig) => {
  srvConfig = newConfig;

  // Fix the hostname according to the server config
  if (srvConfig.hostname) {
    hostname = srvConfig.hostname + (srvConfig.port === 80 ? '' : `:${srvConfig.port}`);
  }

  // If sync is enabled on this server
  if (srvConfig.sync) {
    storage.sync = storage.sync || {};

    qs('#synclabel').className = '';

    if (!storage.sync.channelName) {
      storage.sync.channelName = generateChannelName();
    }

    let channelName = storage.sync.channelName;

    qs('#syncchannel').value = channelName;

    if (storage.settings.autosync) {
      startSync(channelName);
    }

    onEvent('#dosyncbtn', 'click', () => {
      channelName = qs('#syncchannel').value;

      startSync(channelName);
    });
  }
});



