import description from 'raw-loader!./description.md';
import code from 'raw-loader!./code';
import rawdata from 'raw-loader!./data';

import registry from '../registry';

function page() {
  const data = `${rawdata}\n\nreturn data;`;

  function init({ codeEditor, dataEditor }) {
    // codeEditor.addMarker( 3, 0, 4, 0, "editor_green", "fullLine" );
  }

  return {
    description,
    code,
    data,
    init,
    name: '1. Get to know the editor'
  };
}

registry.register('get-to-know-the-editor', page());



