import 'promise-polyfill';
import 'array.prototype.fill';
import 'ace-builds/src-noconflict/ace';
import 'ace-builds/src-noconflict/mode-javascript';

import runScript from 'run-script';

import picassoPkg from '@qlik/picasso/package.json';
import pkg from '../package.json';

import './style.less';
import './settings';
import { picasso as picassoDefault, marked } from './core/loader';

import './dynver';
import './dynver/livereload';
import './core/config';

import generator from './core/generators/hypercube-generator';
import customGenerator from './core/generators/custom-generator';

import { qs, qsAll } from './core/dom/queryselect';
import { onEvent, onWindowEvent } from './core/dom/events';

import createEditor from './core/editor';

import { registry as pages, getIndexPageKey, rendererPagesList } from './core/pages';

import './sense';
import './sync';

import lifecycle from './core/lifecycle';

import loadPlugins from './plugins';

let data;
let element;
let status;
let codeEditor;
let dataEditor;
let latestError;

let picasso = picassoDefault;
let currentChart;

loadPlugins(picasso);

marked.setOptions({
  gfm: true
});

function setSimpleStatus(message, details, error = false) {
  status.className = error ? 'error' : '';

  status.innerHTML = `<h1>${message}</h1> <p>${details || 'No details available'}</p>`;
}

/**
 * Set a fatal error
 */
function setFatalError(message, stack, error) {
  let msg = `<h1>${message}</h1>`;

  if (stack) {
    msg += `<code class="stack">${stack.replace(/</g, '&lt;').replace(/>/g, '&gt;')}</code>`;
  }

  if (error) {
    msg += '<a href="" id="throw-error-to-console">Show error in console</a>';
  }
  latestError = error || false;

  if (!window.preventError) {
    status.innerHTML = msg;
    status.className = 'error';
  }
}

function setVersion(sbxVersion, picassoVersion) {
  qs('#version').innerHTML = `Sandbox ${sbxVersion} Picasso ${picassoVersion}`;

  const href = `picasso/${picassoVersion}/docs/`;
  const docNavButtons = qsAll('.docnavbutton');

  for (let i = 0; i < docNavButtons.length; i++) {
    docNavButtons[i].setAttribute('href', href);
  }

  qs('#dociframe').setAttribute('src', href);
}

function updateChart(props) {
  try {
    currentChart.update(props);
  } catch (err) {
    setFatalError(`Error during update: ${err.message}`, err.stack, err);
  }
}

function destroyChart() {
  try {
    destroyChart();
    currentChart = null;
  } catch (err) {
    setFatalError(`Error during chart destroy: ${err.message}`, err.stack, err);
  }
}

/**
 * Function to execute the code block
 *
 * @param  {String} content The content to be executed
 * @return {Undefined}
 */
function refreshCode(content, event = { type: 'none' }) {
  if (event.type === 'kill') {
    return;
  }

  // Trigger code lifecycle event
  lifecycle.trigger('code', { content, event });

  const result = runScript(content, { picasso, data, element, generator, customGenerator });
  codeEditor.refreshAnnotations();

  if (result && result.error) {
    setFatalError(`Error in code: ${result.error.message}`, result.error.stack, result.error);
  } else if (result && !result.destroy) {
    if (!currentChart) {
      currentChart = picasso.chart(result);
      updateChart({ settings: result.settings });
      setSimpleStatus('Status OK', 'Created new chart');
    } else {
      updateChart({ settings: result.settings });
      setSimpleStatus('Status OK', 'Updated existing chart');
    }
  } else if (result && result.destroy) {
    if (currentChart && currentChart.destroy) {
      destroyChart();
    }

    currentChart = result;
    setSimpleStatus('Status OK', 'Replaced old chart');
  } else {
    if (currentChart && currentChart.destroy) {
      destroyChart();
    }

    const title = 'No picasso.js instance returned';
    const desc = 'Please use return picasso.chart(), otherwise event listeners will \nremain between switching examples and resizing will not be smooth.';

    codeEditor.addAnnotation({
      row: 0,
      column: 0,
      text: `${title}\n${desc}`,
      type: 'warning' // also warning and information
    });

    setSimpleStatus(title, desc);
  }
}

lifecycle.on('resize', () => {
  if (codeEditor) {
    codeEditor.editor.resize();
    dataEditor.editor.resize();
    refreshCode(codeEditor(), { type: 'resize' });
  }
});

lifecycle.on('data', (obj) => {
  data = obj.data;
  if (currentChart && obj.event && obj.event.type !== 'loadPage') {
    updateChart({ data });
  } else {
    refreshCode(codeEditor(), obj.event);
  }
});

lifecycle.on('rendererprio', (obj) => {
  picasso.renderer.prio(obj.prio);
  refreshCode(codeEditor(), { type: 'renderer-prio' });
});

lifecycle.on('picassoload', (newPicasso, version) => {
  picasso = newPicasso;
  loadPlugins(picasso, version, () => {
    refreshCode(codeEditor(), { type: 'picassoLoad' });
  });
  setVersion(`v${pkg.version}`, version);
});

lifecycle.on('server-shutdown', () => {
  setFatalError('Sync server is shutting down. Please wait a couple of minutes and then refresh pages and press sync again.');
});

lifecycle.on('storage-failure', () => {
  setFatalError('LocalStorage failed to save properly. Your storage is either full or unsupported by the browser.');
});

/**
 * Function to execute the data block
 * (and then automatically runs code block afterwards)
 *
 * @param  {String} content The content to be executed
 * @return {Undefined}
 */
function refreshData(content, event = { type: 'none' }) {
  if (event.type === 'kill') {
    return;
  }

  const tmpData = runScript(content, { generator, customGenerator });

  if (!tmpData || typeof tmpData !== 'object') {
    setFatalError(`Data from the data loader should be an object with contents, not ${JSON.stringify(tmpData)}`);
  } else if (tmpData.error) {
    setFatalError(`Error in data: ${tmpData.error.message}`, tmpData.error.stack, tmpData.error);
  } else {
    // Trigger data lifecycle event
    lifecycle.trigger('data', { data: tmpData, event });
  }
}

/**
 * Redraw the pages list
 *
 * @param  {String} [pageName=location.hash] The selected page name
 * @return {Undefined}
 */
function redrawPagesList(pageName = location.hash) {
  pageName = pageName.replace('#', '') || getIndexPageKey();
  document.getElementById('pageslist').innerHTML = rendererPagesList();
  const pageLink = qs(`a[href='#${pageName}']`);

  if (pageLink) {
    pageLink.className = 'selected';
  }
}

/**
 * Load a specific page
 *
 * @param  {String} [pageName=location.hash] Load a specific page
 * @return {Undefined}
 */
export default function loadPage(pageName = location.hash) {
  destroyChart();

  pageName = pageName.replace('#', '') || getIndexPageKey();
  location.hash = pageName;

  const pageData = pages[pageName] || pages['not-found'];

  // Re-renderer the pages list
  redrawPagesList();

  // Re-initalize the code and data editor
  codeEditor(pageData.code || '');
  dataEditor(pageData.data || '');

  // Add listeners, they are reset on page load
  codeEditor.listen(refreshCode);
  dataEditor.listen(refreshData);

  // Reset the rendering area
  document.getElementById('container').innerHTML = 'Nothing rendered yet.';

  // Initalize the page (if required)
  if (pageData.init) {
    pageData.init({ codeEditor, dataEditor });
  }

  // Set the page description (if any)
  if (pageData.description) {
    document.getElementById('description').innerHTML = marked(pageData.description);
  }

  // Refresh the data (and code aswell)
  if (picasso) {
    refreshData(dataEditor(), { type: 'loadPage' });
  }
}

/**
 * Initalize the examples.js
 *
 * @return {Undefined}
 */
function init() {
  element = qs('#container');
  status = qs('#status');

  // Allow to get multiple instances of the element
  element.getInstances = function elemGetInstances(num) {
    element.innerHTML = new Array(num).fill("<div class='instance'></div>").join('');
    return element.children;
  };

  // Show the version
  // setVersion(`v${pkg.version}`, 'master');
  qs('#version').innerHTML = `Sandbox v${pkg.version} Picasso v${picassoPkg.version}`;

  // Initalize the editors
  codeEditor = createEditor('codeeditor');
  dataEditor = createEditor('dataeditor');

  // Init searching
  onEvent('#searchbox', 'keyup', function searchBoxKeyUp() {
    redrawPagesList();
    const searchFor = this.value.toLowerCase();
    if (searchFor) {
      qsAll(`ul li.item:not([title*='${searchFor}'])`).forEach((item) => {
        if (item.className !== 'selected') {
          item.className = 'hide';
        }
      });
    }
  });

  // Change renderer
  onEvent('#renderer-set-canvas', 'click', () => {
    lifecycle.trigger('rendererprio', { prio: ['canvas'] });
    qs('#renderer-set-canvas').className = 'active';
    qs('#renderer-set-svg').className = '';
  });

  onEvent('#renderer-set-svg', 'click', () => {
    lifecycle.trigger('rendererprio', { prio: ['svg'] });
    qs('#renderer-set-svg').className = 'active';
    qs('#renderer-set-canvas').className = '';
  });

  // Show (throw) error in console
  onEvent('#throw-error-to-console', 'click', (e) => {
    e.preventDefault();
    if (latestError) {
      throw latestError;
    }
  });

  function maximizeThis() {
    const target = qs(this.getAttribute('target') || this.parentElement.getAttribute('target'));
    target.className = 'maximized';

    qs('#minimizebutton').className = '';

    lifecycle.trigger('resize');
  }

  // Maximize something
  onEvent('button.maximize', 'click', maximizeThis);
  onEvent('button.maximize i', 'click', maximizeThis);

  onEvent('button#minimizebutton', 'click', () => {
    const target = qs('.maximized');
    target.className = 'content';

    qs('#minimizebutton').className = 'hide';

    lifecycle.trigger('resize');
  });

  qs('#tab-rendarea').click();
}

/**
 * GLOBAL/WINDOW EVENTS BELOW
 */
onWindowEvent('resize', () => {
  window.requestAnimationFrame(() => {
    if (currentChart && currentChart.update) {
      updateChart();
    } else {
      refreshCode(codeEditor(), { type: 'resize' });
    }
  });
});

lifecycle.on('init', init);
lifecycle.on('init', loadPage);

onWindowEvent('hashchange', () => {
  loadPage();
});



