import description from 'raw-loader!./description.md';
import code from 'raw-loader!./code';
import rawdata from 'raw-loader!./data';

import registry from '../registry';

function page() {
  const data = `${rawdata}\n\nreturn data;`;

  return {
    description,
    code,
    data,
    name: 'Null Handling'
  };
}

registry.register('null-handling', page());



