import communication from 'enigma.js/dist/enigma';
import json from '../../qix/3.0/schema.json';
import mixins from './mixins/index';

const config = {
  Promise: window.Promise,
  schema: json,
  mixins,
  session: {
    unsecure: true,
    port: 4848,
    host: 'localhost',
    route: 'app/engineData'
  },
  createSocket(url) {
    const ws = new WebSocket(url);

    ws.onerror = function enigmaOnError(e) {
      console.log(e); // eslint-disable-line no-console
      e.preventDefault();
    };
    return ws;
  }
};

const connect = function enigmaConnect() {
  return communication.getService('qix', config).then(qix => qix.global);
};

export default {
  getDocs: () => connect().then(global => global.getDocList()),
  app: name => connect().then(global => global.openApp(name))
};



