import { registry } from '../../pages/pages';
import { onEvent } from './dom/events';

export {
  registry
};

/**
 * Get the first/index key value
 *
 * @return {String}   Index key/value
 */
export function getIndexPageKey() {
  return Object.keys(registry)[0];
}

onEvent('a.delete', 'click', function eventClickDelete(e) {
  e.preventDefault();

  const itemToDelete = this.getAttribute('data-delete');

  if (itemToDelete && registry[itemToDelete] && registry[itemToDelete].delete) {
    registry[itemToDelete].delete();
  }
});

/**
 * Renderer a list of pages in an sorted/unsorted list (<ol>, <ul>)
 *
 * @return {String}   String with output
 */
export function rendererPagesList() {
  const output = ["<li class='header'>Examples</li>"];
  let item = null;

  Object.keys(registry).forEach((key) => {
    item = registry[key];

    if (item.name) {
      if (item.header) {
        output.push(`<li class='header'>${item.name}</li>`);
      } else {
        let html = `<li class='item' title="${item.name.toLowerCase()}"><a href="#${key}">${item.name}</a>`;

        if (item.delete) {
          html += `<a href class='delete' data-delete='${key}'>X</a></li>`;
        }

        output.push(html);
      }
    }
  });

  return output.join('\n');
}



