import storage from '../../src/core/storage';
import popup from '../../src/core/popup';
import migrate from './migrations.js';

import pages from '../registry';

migrate();

function registerSandbox(key, sandbox) {
  const init = function init({ codeEditor, dataEditor }) {
    // Listen for changes made to the code & data editor
    codeEditor.listen((value) => {
      sandbox.code = value;
    });

    dataEditor.listen((value) => {
      sandbox.data = value;
    });

    // If we're not on the first run then select the renderer area
    if (sandbox.code !== '// Please edit something') {
      document.getElementById('tab-rendarea').click();
    }
  };

  pages.register(`sbx-${key}`, {
    description: '## This sandbox uses localStorage and will remember your code & data over sessions',
    code: () => sandbox.code || '',
    data: () => sandbox.data || '',
    name: key,
    delete: () => {
      popup.confirm('Are you sure you want to delete this?', (result) => {
        if (result === true) {
          delete storage.sandboxes[key];
          delete pages.registry[`sbx-${key}`];
          location.hash = '#';
        }
      });
    },
    init
  });
}

pages.registry.sandbox = {
  header: true,
  name: 'Sandbox'
};

Object.keys(storage.sandboxes).forEach((key) => {
  const sandbox = storage.sandboxes[key];

  registerSandbox(key, sandbox);
});

const newSandboxInit = function newSandboxInit() {
  popup.prompt('Your new sandbox name', 'awesomebox', (name) => {
    if (!name) {
      // Ignore if no name specified
    } else if (storage.sandboxes[name]) {
      popup.alert('This name is already taken, sorry!');
      location.hash = `sbx-${name}`;
      return;
    } else {
      storage.sandboxes[name] = { code: '// Please edit something', data: '// My new sandbox data generator' };
    }

    delete pages.registry['new-sandbox'];
    registerSandbox(name, storage.sandboxes[name]);
    pages.registry['new-sandbox'] = {
      name: '+ Add new sandbox',
      init: newSandboxInit
    };

    if (name) {
      location.hash = `sbx-${name}`;
    } else {
      location.hash = '#';
    }
  });
};

pages.registry['new-sandbox'] = {
  name: '+ Add new sandbox',
  init: newSandboxInit
};



