/* eslint no-alert: 0 */

function popup(msg) {
  popup.alert(msg);
}

popup.alert = function (msg) {
  alert(msg);
};

popup.confirm = function (msg, callback) {
  const result = confirm(msg);
  callback(result);
};

popup.prompt = function (msg, name, callback) {
  const result = prompt(msg, name);
  callback(result);
};

export default popup;



