/* eslint no-confusing-arrow: 0 */

import comm from './comm';
import lifecycle from '../core/lifecycle';

let connectedApp;
// let sheetList;
// let objectList;
let liveObject;
let objectLayout;

let tree;

let senseDataEnabled = false;

function render() {
  if (!objectLayout) {
    return;
  }

  const data = [{
    type: 'q',
    key: 'qHyperCube',
    data: objectLayout.qHyperCube,
    qObject: liveObject.obj
  }];

  // Trigger data lifecycle event
  lifecycle.trigger('data', { data, event: { type: 'sense-com-update' } });
}

function populateList(id, arr, textProp, valueProp) {
  const sel = document.querySelector(id);
  sel.innerHTML = '';
  const text = typeof textProp === 'function' ? textProp : v => v[textProp];
  const value = (v, i) => typeof valueProp !== 'undefined' ? v[valueProp] : i;

  const opt = document.createElement('option');
  opt.setAttribute('value', -1);
  opt.textContent = '<Select a value>';
  sel.appendChild(opt);

  arr.forEach((a, i) => {
    const elm = document.createElement('option');
    elm.setAttribute('value', value(a, i));
    elm.textContent = text(a, i);
    sel.appendChild(elm);
  });
}

function createList(list) {
  tree = list.map((l) => {
    const sheet = {
      title: l.qMeta.title,
      objects: l.qData.cells.map((o) => {
        const obj = {
          type: o.type,
          id: o.name,
          title: ''
        };
        connectedApp.getObject(o.name).then((object) => {
          object.getProperties().then((props) => {
            obj.title = props.title || '[no title]';
          });
        });
        return obj;
      })
    };

    return sheet;
  });

  setTimeout(() => {
    const ul = document.querySelector('#objectsList');
    ul.innerHTML = '';
    tree.forEach((t) => {
      const li = document.createElement('li');
      li.textContent = t.title;
      li.className = 'sheet';
      ul.appendChild(li);

      t.objects.forEach((o) => {
        const elm = document.createElement('li');
        elm.textContent = `${o.type} - ${o.title}`;
        elm.className = 'object';
        ul.appendChild(elm);
        elm.setAttribute('data-id', o.id);
      });
    });
  }, 500); // to ensure promises are resolved (hopefully)
}

function onAppSelect(v) {
  if (+v < 0) {
    return;
  }
  comm.app(v).then((app) => {
    connectedApp = app;
    app.getSheetList().then((list) => {
      if (list.qAppObjectList) {
        list = list.qAppObjectList.qItems;
      }
      // sheetList = list;
      createList(list);
      // populateList( "#sheets", sheetList, a => a.qMeta.title );
    });
  });
}

