import lifecycle from './lifecycle';

const key = '_picassoExampleStorage';
const storageAvailable = ('localStorage' in window);

let lstorage;

if (storageAvailable) {
  if (window.localStorage[key]) {
    lstorage = JSON.parse(window.localStorage[key]);
  } else {
    lstorage = {};
    window.localStorage[key] = {};
  }

  setInterval(() => {
    if (window.localStorage[key]) {
      try {
        window.localStorage[key] = JSON.stringify(lstorage);
      } catch (e) {
        lifecycle.trigger('storage-failure', e);
      }
    }
  }, 500);
}

const storage = lstorage;

export default storage;



