import lifecycle from '../core/lifecycle';
import storage from '../core/storage';
import { qs } from '../core/dom/queryselect';
import { onEvent } from '../core/dom/events';

let settings = {};

function verticalModeChanged() {
  if (settings.vertical) {
    qs('body').className = 'vertical';
  } else {
    qs('body').className = '';
  }
  lifecycle.trigger('resize');
}

lifecycle.on('init', () => {
  storage.settings = storage.settings || {};

  settings = storage.settings;

  onEvent('#togglevertical', 'click', () => {
    settings.vertical = !settings.vertical;
    verticalModeChanged();
  }, true);

  verticalModeChanged();
  window.sandboxSettings = settings;
});



