import code from 'raw-loader!./code';
import rawdata from 'raw-loader!./data';

import registry from '../registry';

function page() {
  const data = `${rawdata}\n\nreturn data;`;

  return {
    description: '# Automatic Scatterplot Example \n Created by MEK for Qlik Sense integration',
    code,
    data,
    name: 'Automatic scatterplot'
  };
}

registry.register('automatic-scatterplot', page());



