import description from 'raw-loader!./description.md';
import rawcode from 'raw-loader!./code';
import rawdata from 'raw-loader!./data';

import registry from '../registry';

function page() {
  const data = `${rawdata}\n\nreturn data;`;
  const code = `${rawcode}\n\nreturn chart;`;

  return {
    description,
    code,
    data,
    name: 'Reference lines'
  };
}

registry.register('reference-lines', page());



