function metric(m) {
  return {
    qInfo: {
      qId: `value_${m}`,
      qType: 'foo'
    },
    qHyperCubeDef: {
      qDimensions: [],
      qMeasures: [
        { qDef: { qDef: m } }
      ],
      qInterColumnSortOrder: [0],
      qInitialDataFetch: [{
        qTop: 0,
        qLeft: 0,
        qWidth: 1,
        qHeight: 10
      }]
    }
  };
}

const defaultDataDef = {
  qInfo: {
    qId: 'data',
    qType: 'bar'
  },
  qHyperCubeDef: {
    qDimensions: [
      { qDef: { qFieldDefs: ['Dim2'] } }
    ],
    qMeasures: [
      { qDef: { qDef: 'Min(Expression1)' } },
      { qDef: { qDef: 'Fractile(Expression1, 0.25)' } },
      { qDef: { qDef: 'Median(Expression1)' } },
      { qDef: { qDef: 'Fractile(Expression1, 0.75)' } },
      { qDef: { qDef: 'Max(Expression1)' } }
    ],
    qMode: 'S',
    qInitialDataFetch: [{ qTop: 0, qHeight: 166, qLeft: 0, qWidth: 6 }]
  }
};

export default {
  types: ['Doc'],
  extend: {
    getPicassoData(dataDef, fn) {
      dataDef = dataDef.qInfo ? dataDef : defaultDataDef;
      return this.getObject(dataDef.qInfo.qId).then(obj => this.Promise.resolve(obj || this.createSessionObject(dataDef)).then((api) => {
        api.layoutSubscribe((layout) => {
          fn(layout);
        });
        return api;
      }));
    },
    getValue(m, fn) {
      const def = metric(m);
      return this.getObject(def.qInfo.qId).then(obj => this.Promise.resolve(obj || this.createSessionObject(def)).then((api) => {
        api.layoutSubscribe((layout) => {
          fn({
            value: layout.qHyperCube.qDataPages[0].qMatrix[0][0],
            info: layout.qHyperCube.qMeasureInfo[0]
          });
        });
        return api.getLayout();
      }));
    }
  }
};



